*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;

    @media only screen and (max-width: $bp-tablet) {
        font-size: 55%;
    }

    @media only screen and (max-width: $bp-phone) {
        font-size: 50%;
    }
}


body {
    font-family: $font-primary;
    font-weight: 400;
    line-height: 1.7;
    color: $text-color;
}